import { Fragment, useEffect, useRef, useState } from "react";
import { Menu, InputNumber, Button, Drawer, Slider, Radio, Divider, Spin, Input } from "antd";
import { LoadingOutlined } from '@ant-design/icons';
import { MenuUnfoldOutlined } from "@ant-design/icons";
import { useHover } from "../../utils";
import { IBaseProps, Range } from "../../types";
import { animated, config, useSpring } from "react-spring/web";
import './console.scss'

const { Item, SubMenu } = Menu;

interface IConsoleProps extends IBaseProps {
    /**  */
    operation?: React.ReactNode;
    /**  */
    displayer?: React.ReactNode;
    /** drawer */
    drawerHeight?: number;
    /** silider */
    showSilider?: boolean;
    /**  */
    isUpdate?: boolean;
    /**  */
    isSearch?: boolean;
    /**  */
    isAddIndex?: boolean;
    /**  */
    isDeleteIndex?: boolean;
    /** value */
    valueRange?: Range;
    /** index */
    indexRange?: Range;
    /** value */
    defaultValue?: number;
    /** index */
    defaultIndex?: number;
    /** searchValue */
    defaultSearchValue?: number;
    /**  */
    addText?: string;
    /**  */
    deleteText?: string;
    /**  */
    spinning?: boolean;
    /** slider */
    onSliderChange?: (value: number) => void;
    /** value */
    onValueChange?: (value: number) => void;
    /** index */
    onIndexChange?: (index: number) => void;
    /** searchValue */
    onSearchValueChange?: (value: number) => void;
    /**  */
    onAdd?: (value: number, index: number) => void;
    /**  */
    onDelete?: (index: number) => void;
    /**  */
    onSearch?: (value: number) => void;
    /**  */
    onRenderChange?: (value: string) => void;
    /**  */
    onRender?: (value: string) => void;
}

const Console: React.FC<IConsoleProps> = (props) => {

    const {
        children,
        style,
        operation,
        displayer,
        drawerHeight,
        showSilider,
        addText,
        defaultIndex,
        defaultSearchValue,
        valueRange,
        indexRange,
        defaultValue,
        deleteText,
        isUpdate,
        isAddIndex,
        isDeleteIndex,
        isSearch,
        spinning,
        onSliderChange,
        onAdd,
        onDelete,
        onSearch,
        onIndexChange,
        onValueChange,
        onSearchValueChange,
        onRenderChange,
        onRender
    } = props;

    const [hoverLeftRef, isLeftHover] = useHover();
    const [hoverRenderRef, isRenderHover] = useHover();

    const [isUnfold, setIsUnfold] = useState(false);
    /** valueindex */
    const [value, setValue] = useState(defaultValue || 8);
    const [index, setIndex] = useState(defaultIndex || 0);
    const [searchValue, setSearchValue] = useState(defaultSearchValue || 32);
    const [renderValue, setRenderValue] = useState('');

    //  radio
    const [radioActived, setRadioActived] = useState(1);

    const displayConRef = useRef<HTMLDivElement>();
    const { leftOpacity, renderOpacity } = useSpring({
        leftOpacity: isLeftHover ? 0.7 : 0.2,
        renderOpacity: isRenderHover ? 0.7 : 0.2,
        config: config.gentle
    })

    /**  displayer  */
    useEffect(() => {
        if (displayConRef.current) displayConRef.current.scrollTop = displayConRef.current.scrollHeight;
    }, [displayConRef.current?.scrollHeight])

    return (
        <>
            <animated.div
                className='console-left'
                ref={hoverLeftRef as any}
                style={{ ...style, opacity: leftOpacity }}
            >
                {/*  */}
                <Menu
                    className='console'
                    mode="inline"
                    theme="dark"
                    inlineCollapsed={true}
                    selectable={false}
                    style={{ display: isUnfold ? 'none' : 'inline-block' }}
                >
                    <Item
                        icon={<MenuUnfoldOutlined />}
                        key='item0'
                        onClick={() => {
                            setIsUnfold(true);
                        }}
                    >
                        
                </Item>
                    {children}
                </Menu>
                {/*  */}

                {/*  */}
                <Drawer
                    className='console-drawer'
                    title=''
                    height={drawerHeight}
                    visible={isUnfold}
                    placement='bottom'
                    mask={false}
                    onClose={() => { setIsUnfold(false) }}
                >

                    <div className='operation'>
                        {showSilider &&
                            <div className='slider-warp'>
                                
                            <Slider
                                    className='slider'
                                    defaultValue={32}
                                    onChange={(value: number) => onSliderChange?.(value)}
                                />
                            </div>
                        }


                        <Spin
                            tip='...'
                            spinning={spinning}
                            indicator={<LoadingOutlined style={{ fontSize: 40 }} spin />}
                        >
                            <div className='operation-main'>
                                {/*  */}
                                {operation}

                                {/*  */}
                                {
                                    isUpdate &&
                                    <div className='input-group'>
                                        <Radio.Group
                                            className='radio-group'
                                            defaultValue={1}
                                            onChange={(e) => setRadioActived(e.target.value)}
                                        >
                                            <Radio value={1}>{addText}</Radio>
                                            <Radio value={2}>{deleteText}</Radio>
                                            {isSearch && <Radio value={3}></Radio>}
                                        </Radio.Group>

                                        <div className='label-group'>
                                            {
                                                radioActived === 1 ?
                                                    (
                                                        <>
                                                            {
                                                                isAddIndex &&
                                                                // input
                                                                (<label>
                                                                    <span className='label-name'>:</span>
                                                                    <InputNumber
                                                                        // min={(indexRange as unknown as number[])?.[0]}
                                                                        // max={(indexRange as unknown as number[])?.[0]}
                                                                        defaultValue={defaultIndex}
                                                                        onChange={(index) => {
                                                                            setIndex(index as number)
                                                                            onIndexChange?.(index)
                                                                        }}
                                                                    />
                                                                </label>)
                                                            }
                                                            {/* input */}
                                                            <label>
                                                                <span className='label-name'>:</span>
                                                                <InputNumber
                                                                    // min={(valueRange as unknown as number[])?.[0]}
                                                                    // max={(valueRange as unknown as number[])?.[1]}
                                                                    defaultValue={defaultValue}
                                                                    onChange={(value) => {
                                                                        setValue(value as number)
                                                                        onValueChange?.(value)
                                                                    }}
                                                                />
                                                            </label>
                                                        </>
                                                    ) : radioActived === 2 ?
                                                        (
                                                            isDeleteIndex &&
                                                            // input
                                                            (<label>
                                                                <span className='label-name'>:</span>
                                                                <InputNumber
                                                                    // min={(indexRange as unknown as number[])?.[0]}
                                                                    // max={(indexRange as unknown as number[])?.[0]}
                                                                    defaultValue={defaultIndex}
                                                                    onChange={(index) => {
                                                                        setIndex(index as number)
                                                                        onIndexChange?.(index)
                                                                    }}
                                                                />
                                                            </label>)
                                                        ) : isSearch &&
                                                        (
                                                            // input
                                                            (<label>
                                                                <span className='label-name'>:</span>
                                                                <InputNumber
                                                                    // min={(valueRange as unknown as number[])?.[0]}
                                                                    // max={(valueRange as unknown as number[])?.[0]}
                                                                    defaultValue={defaultSearchValue}
                                                                    onChange={(value) => {
                                                                        setSearchValue(value as number)
                                                                        onSearchValueChange?.(value)
                                                                    }}
                                                                />
                                                            </label>)
                                                        )
                                            }

                                            {radioActived === 1 && <Button type='primary' onClick={() => onAdd?.(value, index)}>{addText}</Button>}
                                            {radioActived === 2 && <Button type='primary' onClick={() => onDelete?.(index)}>{deleteText}</Button>}
                                            {radioActived === 3 && <Button type='primary' onClick={() => onSearch?.(searchValue)}></Button>}

                                        </div>
                                    </div>
                                }
                            </div>
                        </Spin>

                    </div>

                    <Divider
                        className='divider'
                        type='vertical'
                    />

                    <div className='displayer'>
                        <div className='content' ref={displayConRef as any}>
                            {displayer}
                        </div>
                    </div>
                </Drawer>
            </animated.div>
            <animated.div
                className='console-render'
                ref={hoverRenderRef as any}
                style={{ ...style ,opacity: renderOpacity }}
            >
                <Input
                    bordered={false}
                    onChange={(e) => {
                        setRenderValue(e.target.value.trim());
                        onRenderChange?.(e.target.value.trim());
                    }}
                />
                <Button type='primary' onClick={() => { onRender?.(renderValue) }}></Button>
            </animated.div>
        </>
    )
}

Console.defaultProps = {
    showSilider: true,
    addText: '',
    deleteText: '',
    defaultIndex: 2,
    defaultValue: 3,
    defaultSearchValue: 27,
    valueRange: [3, 90] as Range,
    indexRange: [0, 10] as Range,
    isUpdate: true,
    isSearch: false,
    isAddIndex: true,
    isDeleteIndex: true,
    spinning: false
}

export { Item, SubMenu };
export default Console;




